.carousel {
    width: 100%;
  }
  
  .carousel__container {
    display: flex;
    flex-direction: row;
    margin: 0 0 20px 0;
    overflow-x: scroll;
    padding: 0;
    scroll-snap-type: x mandatory;
    scrollbar-width: none;
    -ms-overflow-style: none;
    -webkit-overflow-scrolling: touch;
  }
  
  .carousel__container::-webkit-scrollbar {
    display: none;
  }
  
  .carousel__slide {
    min-width: 96vw;
    padding: 10px;
    scroll-snap-align: start;
  }
  
  .carousel__slide-content {
  }
  
  .carousel__slide-body-type {
    color: var(--color-grey2);
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 5px;
    text-transform: uppercase;
  }
  
  .carousel__slide-model-name {
    color: var(--color-grey1);
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 5px;
  }
  
  .carousel__slide-model-type {
    color: var(--color-grey2);
    font-size: 14px;
    margin-bottom: 10px;
  }
  
  .carousel__slide-image {
    margin-bottom: 20px;
    max-width: 100%;
  }
  
  .carousel__slide-links {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  .carousel__slide-link {
    color: var(--color-blue1);
    display: inline-block;
    font-size: 14px;
    font-weight: bold;
    margin: 0 10px;
    text-decoration: none;
    text-transform: uppercase;
  }
  
  .carousel__slide-link::after {
    background: center / contain no-repeat url('./images/chevron-small.svg');
    content: '';
    display: inline-block;
    height: 10px;
    margin-left: 8px;
    width: 10px;
  }
  
  .carousel__indicators {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .carousel__indicator-item {
    background-color: #ebebeb;
    border-radius: 5px;
    cursor: pointer;
    height: 10px;
    margin: 0 5px;
    width: 10px;
  }
  
  .carousel__indicator-item__selected {
    background-color: var(--color-grey1);
  }
  
  .carousel__navigation {
    display: none;
    flex-direction: row;
    justify-content: flex-end;
    margin-bottom: 20px;
    padding: 0 10px;
  }
  
  .carousel__navigation-item {
    margin-left: 10px;
  }
  
  .carousel__navigation-button {
    background: center / contain no-repeat url('./images/chevron-circled.svg');
    border: none;
    cursor: pointer;
    display: inline-block;
    height: 30px;
    text-indent: -9999em;
    width: 30px;
  }
  
  .carousel__navigation-button__previous {
    transform: rotate(180deg);
  }
  
  .carousel__navigation-button__next {
  }
  
  .sr {
    border: 0;
    clip: rect(0, 0, 0, 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  
  @media (min-width: 768px) {
    .carousel__slide {
      min-width: calc(25vw - 20px);
      width: calc(25vw - 20px);
    }
  
    .carousel__indicators {
      display: none;
    }
  
    .carousel__navigation {
      display: flex;
    }
  }
  